<template>
    <div class="sworks"> 
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-folder-opened"></i> 基础资料
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="form">
                <div class="form-top">
                    <el-button class="fr" type="primary" @click="editVisible3=true" size="small">新建文件夹</el-button>
                </div>
                <div class="cont" v-loading="loading">
                    <el-table
                      :data="tableData"
                      style="width: 100%;margin-top:20px;">
                      <el-table-column
                        type="index"
                        width="100px"
                        label="序号">
                      </el-table-column>
                      <el-table-column
                        prop="name"
                        label="文件夹名称">
                      </el-table-column>
                      <el-table-column
                        prop="num"
                        label="文件数量">
                      </el-table-column>
                      <el-table-column
                        prop="createtime"
                        label="创建时间">
                      </el-table-column>
                      <el-table-column label="操作" width='180px'>
                          <template slot-scope="scope">
                            <el-button
                              size="mini"
                              type="primary" plain
                              @click="handleEdit(scope.row.id)">管理文件</el-button>
                            <el-button
                              size="mini"
                              type="danger"
                              plain
                              @click="delworks(scope.row.id)">删除</el-button>
                          </template>
                        </el-table-column>
                    </el-table>

                    <div class="pagination">
                          <el-pagination
                            background
                            :page-size="pageSize"
                            @current-change="changePage"
                            layout="prev, pager, next"
                            :total="total">
                          </el-pagination>
                      </div>
                    
                </div>
            </div>
        </div>
        <!-- 编辑弹出框 -->
        <el-dialog title="添加资料" class="datas-bg" :visible.sync="editVisible" width="800px">
            <Sdatas2 :folderId="folderId" />
        </el-dialog>
        <!-- 新建入学年份 -->
        <el-dialog title="新建文件夹" class="datas-bg" :visible.sync="editVisible3" width="380px">
            <el-input class="margin10" v-model="name" size="small" placeholder="文件夹名称"></el-input>
            <div class="buttonbg" >
              <el-button type="primary" size="small" @click="Addfolder">确 定</el-button>
              <el-button type="info" size="small" @click="editVisible3=false" plain>取 消</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import {fetchListfolder, fetchAddfolder, fetchDeletefolder} from '../../axios/requests'
import Sdatas2 from './Sdatas2.vue'
export default {
    name: 'course',
    components:{Sdatas2},
    data() {
        return {
          loading:false,
          pageNum:1,
          pageSize:10,
          total:0,

          name:'',
          folderId:'',

          editVisible:false,
          editVisible3:false,
          choosed:'未完成课程设计教学',
          items:[1,1,1],
          tableData: []
        };
    },
    mounted(){
      this.Listfolder();
    },
    methods: {
        openLink(){
            this.$router.push({path:'newworks'})
        },
        handleEdit(id){
            this.folderId = id;
            this.editVisible=true;
        },
        delworks(id){
            this.$confirm('确定要删除吗？', '提示', {
                type: 'warning'
            })
            .then(() => {
                this.Deletefolder(id)
            })
            .catch(() => {});
        },
        changePage(page){
          this.pageNum = page;
          this.Listfolder();
        },
        async Listfolder() {//得到文件夹列表
          this.loading = true;
          let {pageNum, pageSize} = this;
          try {
            let response = await fetchListfolder(pageNum, pageSize);
            if (response.status.code ==0) {
              this.total = response.result.total;
              this.tableData = response.result.list;
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            this.loading = false;
          }
        },
        async Addfolder(){//新建文件夹
          let {name} = this;
          if (!name) {this.$message.error('文件夹不能为空');return};
           const loading = this.$loading({
            lock: true,
            text: '加载中...',
            spinner: 'el-icon-loading'
          });
          try {
            let response = await fetchAddfolder(name);
            if (response.status.code ==0) {
              this.$message({
                message: '添加成功',
                type: 'success'
              });
              this.editVisible3 = false;
              this.Listfolder() 
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            loading.close()
          }
        },
        async Deletefolder(id) {//删除文件夹
          const loading = this.$loading({
            lock: true,
            text: '加载中...',
            spinner: 'el-icon-loading'
          });
          try {
            let response = await fetchDeletefolder(id);
            if (response.status.code ==0) {
              this.$message({
                message: '删除成功',
                type: 'success'
              });
              this.Listfolder();
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            loading.close()
          }
      },
    }
};
</script>
<style lang='scss'>
    .datas-bg{
         .el-dialog__body{
            padding:10px 20px 30px;
         }
    }
</style>
<style lang='scss' scoped>
    .sworks{
        .form-top{
            overflow:hidden;
        }
    }
    .pagination{
        margin-top:20px;
    }
    .buttonbg{
      text-align:center;
      padding-top: 20px;
    }
</style>


