<template>
    <div class="twobg"  v-loading="loading">
        <div class="select-bg">
            <a class="file">选择文件<input class="upload" @change='add_img' :value="filevalue" type="file"></a>
        </div>
        <el-table
            size="mini"
            ref="multipleTable"
            class="editlist"
            :data="tableData"
            tooltip-effect="dark"
            height="300px"
            style="width: 100%">
            <el-table-column
              type="index"
              label="序号">
            </el-table-column>
            <el-table-column
              prop="name"
              label="文件名称">
            </el-table-column>
            <el-table-column
              prop="size"
              label="文件大小">
            </el-table-column>
            <el-table-column
              prop="creatorName"
              label="上传者">
            </el-table-column>
            <el-table-column
              width="140px"
              prop="createtime"
              label="创建时间">
            </el-table-column>
            <el-table-column
              prop="useCount"
              label="使用次数">
            </el-table-column>
            <el-table-column label="操作" >
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="danger"
                  @click="delworks(scope.row.id)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="pagination">
              <el-pagination
                background
                :page-size="pageSize"
                @current-change="changePage"
                layout="prev, pager, next"
                :total="total"> 
              </el-pagination>
          </div>
    </div>
</template>

<script>
import axios from "axios";
import cookie from '../../utils/cookie'
import {url, fetchGetFileByFolderId, fetchDeleteFile, fetchAddFile} from '../../axios/requests'
export default {
    name: 'Sdatas',
    props: ['folderId'],
    data() {
        return {
          loading:false,
          pageNum:1,
          pageSize:10,
          total:0,
          Id:'',
          tableData: [],
          filevalue:''
        };
    },
     watch:{
      folderId: {
          handler(newVal, oldVal) {
            this.Id = newVal;
            this.GetFileByFolderId()
          },
          deep: false,
          immediate: true
        }
      },
    mounted(){
      
    },
    methods: {
        delworks(id){
          this.$confirm('确定要删除吗？', '提示', {
                type: 'warning'
            })
            .then(() => {
                this.DeleteFile(id)
            })
            .catch(() => {});
        },
        add_img(event){ 
          const loading = this.$loading({
            lock: true,
            text: '加载中...',
            spinner: 'el-icon-loading'
          });
          let reader =new FileReader();
          let img1=event.target.files[0];
          let form = new FormData(); 
          form.append('file',img1,img1.name);
          form.append('folderId',this.Id);
          axios.post(url+'/file/addFile/',form,{
            headers:{'Content-Type':'multipart/form-data','teacherToken':cookie.getCookie('teacherToken')}
          }).then(response => {
            loading.close()
            this.pageNum = 1;
            this.$message({
              message: '上传成功',
              type: 'success'
            });
            this.GetFileByFolderId();
          }).catch(error => {
            loading.close()
            this.filevalue = '';
          })  
        },
        changePage(page){
          this.pageNum = page;
          this.GetFileByFolderId();
        },
        async GetFileByFolderId() {//得到文件列表
          this.loading = true;
          let {pageNum, pageSize, Id} = this;
          try {
            let response = await fetchGetFileByFolderId(pageNum, pageSize, Id);
            if (response.status.code ==0) {
              this.total = response.result.total;
              this.tableData = response.result.list;
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            this.loading = false;
          }
       },
      async DeleteFile(id) {//删除文件
          const loading = this.$loading({
            lock: true,
            text: '加载中...',
            spinner: 'el-icon-loading'
          });
          try {
            let response = await fetchDeleteFile(id);
            if (response.status.code ==0) {
              this.$message({
                message: '删除成功',
                type: 'success'
              });
              this.GetFileByFolderId();
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            loading.close()
          }
      },
    }
};
</script>
<style lang='scss'>
  .cont{
      tbody{
        .el-table__expanded-cell{
          padding:5px 0 5px 60px;
        }
      }
    }
</style>
<style lang='scss' scoped>
    .red{
      color:red;
      font-size:12px;
      line-height:16px;
      padding-top:5px;
    }
    .buttonbg{
      text-align:center;
      padding-top: 20px;
    }
    .cont{
      padding-top:20px;
      .class-bg{
        overflow:hidden;
        line-height:32px;
        font-size:13px;
        color:#666;
      }
      .college{
        margin-right:-10px;
      }
    }
    .pagination{
        margin-top:20px;
    }
    .file {
        float:right;
        margin-bottom:10px;
        cursor:pointer;
        position: relative;
        display: inline-block;
        background: #D0EEFF;
        border: 1px solid #99D3F5;
        border-radius: 4px;
        padding: 4px 12px;
        overflow: hidden;
        color: #1E88C7;
        text-decoration: none;
        text-indent: 0;
        line-height: 20px;
    }
    .file input {
        position: absolute;
        font-size: 100px;
        right: 0;
        top: 0;
        opacity: 0;
        cursor:pointer;
    }
    .file:hover {
        background: #AADFFD;
        border-color: #78C3F3;
        color: #004974;
        text-decoration: none;
    }
</style>


